import Vue from 'vue'
import Vuex from 'vuex'
import sentences from './sentences.js';

Vue.use(Vuex)

export default new Vuex.Store({
  state: {
    sentences,
    open: [],
  },
  getters: {
    sentence: (id) => s => s.sentences[id],
    hasSentences: s => s.open.length > 0,
    openSentences: s => s.open.map((i) => s.sentences[i]),
  },
  mutations: {
    generate(s) {
      if (s.open.length === s.sentences.length) return;
      let n = Math.floor(Math.random() * s.sentences.length);
      while (s.open.indexOf(n) !== -1) {
        n = Math.floor(Math.random() * s.sentences.length);
      }
      s.open.unshift(n);
    },
    close(s, index) {
      s.open = s.open.filter((i, ind) => ind !== index);
    },
  },
  actions: {
    share({ commit }, id) {
      return new Promise((resolve, reject) => {

      });
    },
  },
});
