<template>
  <div class="Screen LongScreen">
    <div class="Windows">
      <window v-for="(w, index) in windows" :sentence="w" :key="index"
        @focus="currentWindow=index"
        @close="close(index)"
        :index="index"
        :height="index === currentWindow ? 1 : 0"
        @share="share(index)"></window>
    </div>
  </div>
</template>

<script>
  import Window from './Window.vue';

  export default {
    name: 'Windows',
    data() {
      return {
        currentWindow: 0,
      };
    },
    components: {
      Window,
    },
    props: {
      windows: {},
    },
    methods: {
      close(index) {
        this.$store.commit('close', index);
        this.currentWindow = this.windows.length - 1;
      },
      getIndex(index) {
        return this.$store.state['open'][index];
      },
      share(index) {
        FB.ui({
          method: 'share',
          href: `http://julie-puech.com/genewsrator/news/${this.getIndex(index)}`,
        }, function(response){});
      },
    },
  };
</script>

<style lang="scss">
  .Screen.LongScreen {
    height: auto;
    padding-bottom: 200px;
  }

  .Windows {
    position: relative;
    width: 100%;
    top: -150px;
    padding-bottom: 150px;
  }
</style>